import {withRouter} from "react-router";
import {compose} from "redux";
import {connect} from "react-redux";
import React, {Component, Fragment} from "react";
import makeStyles from "@material-ui/core/styles/makeStyles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import {NavLink} from "react-router-dom";
import PropTypes from "prop-types";
import {withStyles} from "@material-ui/styles";
import {user} from "../data";

const navigationStyles = () => ({
    btn: {
        fontFamily: 'Montserrat',
        paddingLeft: 15,
        paddingRight: 5,
        color: 'white',
        fontSize: 14,
        textDecoration: 'none',

    },
    root: {
        flexGrow: 1,
    },

    title: {
        flexGrow: 1,
        fontFamily: 'Montserrat',
        fontSize: 18,
        textDecoration: 'none',
        color: 'white'
    },
    avatar: {
        fontSize: 40,
    },
    link: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        textDecoration: 'none',
        color: 'white'

    },
    toolbar: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
    },
    actions: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center'
    }
});

class Navigation extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const { classes } = this.props;
        return (
            <div className={classes.root}>
                <AppBar position='fixed' style={{backgroundColor: '#39A298'}}>
                    <Toolbar className={classes.toolbar}>
                        <NavLink to='/' className={classes.link}>
                            <svg width="56" height="44" viewBox="0 0 56 44" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <rect width="56" height="44" fill="url(#pattern0)"/>
                                <defs>
                                    <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
                                        <use xlinkHref="#image0"
                                             transform="translate(0 -0.050882) scale(0.000746269 0.000949796)"/>
                                    </pattern>
                                    <image id="image0" width="1340" height="1160"
                                           xlinkHref="data:image/png;base64,"/>
                                </defs>
                            </svg>
                            <div className={classes.title}>
                                BOOKING
                            </div>
                        </NavLink>
                        <div className={classes.actions}>
                            <Fragment>
                                <div className={classes.btn}>

                                    <svg width="32" height="17" viewBox="0 0 32 17" fill="none"
                                         xmlns="http://www.w3.org/2000/svg">
                                        <rect width="32" height="17" rx="4" fill="url(#pattern1)"/>
                                        <defs>
                                            <pattern id="pattern1" patternContentUnits="objectBoundingBox" width="1"
                                                     height="1">
                                                <use xlinkHref="#image1"
                                                     transform="translate(0 -0.441176) scale(0.00390625 0.00735294)"/>
                                            </pattern>
                                            <image id="image1" width="256" height="256"
                                                   xlinkHref="data:image/png;base64,"/>
                                        </defs>
                                    </svg>
                                </div>
                                <div>$</div>
                            </Fragment>
                            <Fragment>
                                <Button
                                    color='inherit'>

                                    <div className={classes.btn}>
                                        ENG
                                    </div>
                                </Button>
                            </Fragment>
                            <Fragment>
                                <NavLink className={classes.btn} to='/register'>
                                    <Button
                                        color='inherit'>

                                        <div className={classes.btn}>
                                            Sing up
                                        </div>
                                    </Button>
                                </NavLink>
                            </Fragment>
                            <Fragment>
                                <NavLink className={classes.btn} to='/login'>
                                    <Button
                                        color='inherit'>

                                        <div className={classes.btn}>
                                            Sing in
                                        </div>
                                    </Button>
                                </NavLink>
                            </Fragment>

                        </div>


                    </Toolbar>
                </AppBar>
            </div>
        )
    }
}


export default withStyles(navigationStyles)(Navigation);
